@use '../abstracts/colors' as c;
@use '../abstracts/fonts' as f;
@use '../base/mixins' as *;

*,
* :before,
* :after {
  box-sizing: border-box;
}

html,
body,
div,
span,
object,
iframe,
figure,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
code,
em,
img,
small,
strike,
strong,
sub,
sup,
tt,
b,
u,
i,
ol,
ul,
li,
fieldset,
form,
label,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
main,
canvas,
embed,
footer,
header,
nav,
section,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

footer,
header,
nav,
section,
main {
  display: block;

}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  font-family: f.$font-primary;
  color: c.$color-text-grey;
  font-weight: 300;
  line-height: 1.6;
  overflow-x: hidden;
  overflow-y: hidden;
}

html {
  overflow-y: scroll;
  font-size: 62.5%; /*1rem = 10px default font size global*/
  font-weight: 400;
  letter-spacing: 2px;

  @include respond(phone-sm) {
    font-size: f.$font-size-phone-sm;
  }
  @include respond(phone-md) {
    font-size: f.$font-size-phone-md;
  }
 
  @include respond(tab-portrait) {
    font-size: f.$font-size-tab-portrait;
  }
  @include respond(tab-landscape) {
    font-size: f.$font-size-tab-landscape;
  }
  @include respond(laptop) {
    font-size: f.$font-size-laptop;
  }
  @include respond(desktop) {
    font-size: f.$font-size-desk;
  }
  @include respond(desktop-lg) {
    font-size: f.$font-size-desk-lg;
  }

}


